<template>
	<div class="login-pop">
		<!--  -->
		<div class="close" @click="closeLogin">
			<span class="iconfont icon-guanbi1"></span>
		</div>

		<!--  -->
		<div class="phone-login" v-if="loginWay === 0">
			<h2></h2>
			<!-- logo -->
			<div class="imgs">
				<span class="iconfont icon-tel"></span>
			</div>
			<!--  -->
			<div class="form">
				<el-form label-width="70px">
					<el-form-item label="">
						<el-input type="tlephone" v-model="PhoneNum" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input type="password" v-model="Password" placeholder=""></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="phoneLogin"></el-button>
					</el-form-item>
					<el-form-item>
						<el-button @click="changeLoginWay(2)"></el-button>
					</el-form-item>
				</el-form>
			</div>
			<div class="other-way" @click="changeLoginWay(1)" style="margin-top: 50px; padding-left: 25px">
				<span><i class="iconfont icon-erweima1"></i> <i class="text"></i> </span>
			</div>
		</div>

		<!--  -->
		<div class="qr-login" v-if="loginWay === 1">
			<div class="qr">
				<h2></h2>
				<img :src="qrurl" alt="" />
				<p class="failqr" v-if="failqr"><span @click="qrLogin"></span></p>
				<p class="text" v-if="!scanQr"><span style="color: #5091ca">APP</span></p>
				<p class="text" v-if="scanQr"></p>
				<!--  -->
				<div class="other-way" @click="changeLoginWay(0)" style="margin-top: 30px">
					<span>
						<i class="iconfont icon-tel"></i>
						<i class="text"> </i></span
					>
				</div>
				<div class="other-way" style="padding-top: 50px" @click="changeLoginWay(2)">
					<span></span>
				</div>
			</div>
		</div>

		<!--  -->
		<div class="register" v-show="loginWay === 2">
			<h2></h2>
			<div class="reg-form">
				<el-form label-position="right" label-width="80px" :model="ruleForm" :rules="rules" ref="ruleForm">
					<el-form-item label="" prop="phone">
						<el-input type="telphone" v-model="ruleForm.phone" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="pass">
						<el-input type="password" v-model="ruleForm.pass" placeholder="8~20"></el-input>
					</el-form-item>
					<el-form-item label="" prop="checkPass">
						<el-input type="password" v-model="ruleForm.checkPass" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="nickname">
						<el-input type="text" v-model="ruleForm.nickname" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="captcha" class="captcha-input">
						<el-input v-model="ruleForm.captcha" placeholder=""></el-input>
						<div class="captcha-btn" @click="getCaptcha" v-if="!isGetCaptcha"></div>
						<div class="captcha" v-else>{{ tip }}</div>
					</el-form-item>
				</el-form>
			</div>
			<div class="reg-btns">
				<div class="reg-btn">
					<el-button type="primary" @click="register('ruleForm')"></el-button>
				</div>
				<div class="reg-btn">
					<el-button @click="changeLoginWay(0)"></el-button>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import { login, getQrKey, getLoginQr, checkLoginQr, getUserAccount, getAuthcode, checkAuthcode, register, checkPhoneNum } from "network/login/login";
import { getUserSonglist, getUserDetail } from "network/userdetail/userdetail";
import { getLikSongList } from "network/playmusic/playmusic.js";
import { isPhone, throttle } from "common/utils.js";
export default {
	name: "LoginPop",
	data() {
		//
		var validatePhone = (rule, value, callback) => {
			if (value === "") {
				callback(new Error(""));
			} else {
				if (!isPhone(value)) {
					callback(new Error(""));
				}
				callback();
			}
		};
		var validatePass = (rule, value, callback) => {
			if (value === "") {
				callback(new Error(""));
			} else {
				if (this.ruleForm.checkPass !== "") {
					this.$refs.ruleForm.validateField("checkPass");
				}
				callback();
			}
		};
		var validatePass2 = (rule, value, callback) => {
			if (value === "") {
				callback(new Error(""));
			} else if (value !== this.ruleForm.pass) {
				callback(new Error("!"));
			} else {
				callback();
			}
		};
		var validateName = (rule, value, callback) => {
			if (value === "") {
				callback(new Error(""));
			}
			callback();
		};
		var validateCaptcha = (rule, value, callback) => {
			if (value === "") {
				callback(new Error(""));
			}
			callback();
		};
		return {
			PhoneNum: "", //
			Password: "", //
			loginWay: 0, //
			qrurl: "", //
			timer: "", //
			failqr: false, //
			scanQr: false,
			isGetCaptcha: false, //
			tip: "", //
			ruleForm: {
				phone: "", //
				pass: "", //
				checkPass: "", //
				nickname: "", //
				captcha: "", //
			},
			rules: {
				phone: [{ validator: validatePhone, trigger: "blur", required: true }],
				pass: [{ validator: validatePass, trigger: "blur", required: true }],
				checkPass: [{ validator: validatePass2, trigger: "blur", required: true }],
				nickname: [{ validator: validateName, trigger: "blur", required: true }],
				captcha: [{ validator: validateCaptcha, trigger: "blur", required: true }],
			},
		};
	},
	created() {},
	methods: {
		// 
		closeLogin() {
			this.$emit("closeLogin");
			clearInterval(this.timer);
		},
		// 
		changeLoginWay(way) {
			this.loginWay = way;
			// 
			if (way !== 1) {
				clearInterval(this.timer);
				if (way === 0) {
					this.$refs.ruleForm.resetFields(); //
				}
			}
			// 
			if (way === 1) {
				this.qrLogin();
			}
		},

		// 
		async qrLogin() {
			this.failqr = false; // 
			this.scanQr = false;
			// key
			let res = await getQrKey();
			let key = res.data.data.unikey;
			// 
			let res2 = await getLoginQr(key);
			this.qrurl = res2.data.data.qrimg;
			// 
			this.timer = setInterval(async () => {
				let statusRes = await checkLoginQr(key);
				console.log(statusRes.data);
				if (statusRes.data.code === 800) {
					this.$message({
						showClose: true,
						message: "",
						type: "error",
						center: true,
					});
					clearInterval(this.timer);
					// 
					this.failqr = true;
				}
				if (statusRes.data.code === 802) {
					this.scanQr = true;
				}
				if (statusRes.data.code === 803) {
					clearInterval(this.timer);
					this.$message({
						showClose: true,
						message: "",
						type: "success",
						center: true,
					});
					// 
					this.getUserLoginAccount();
				}
			}, 2000);
		},
		//  
		getUserLoginAccount() {
			getUserAccount().then(res1 => {
				let uid = res1.data.account.id;
				// 
				getUserDetail(uid).then(res => {
					this.loginSuccess(res);
				});
			});
		},

		// 
		phoneLogin() {
			if (this.PhoneNum.trim() === "" || this.Password === "") {
				this.$message({
					message: "",
					type: "warning",
					center: true,
				});
				return;
			} else {
				login(this.PhoneNum, this.Password)
					.then(res => {
						if (res.data.code === 200) {
							this.$message({
								showClose: true,
								message: "",
								type: "success",
								center: true,
							});
							// 
							this.loginSuccess(res);
						} else if (res.data.code === 502) {
							this.$message({
								message: "",
								type: "warning",
								center: true,
							});
						} else {
							this.$message({
								message: "",
								type: "warning",
								center: true,
							});
						}
					})
					.catch(err => {
						this.$message({
							message: "",
							type: "warning",
							center: true,
						});
					});
			}
		},

		// 
		loginSuccess(res) {
			// 
			this.closeLogin();
			// 
			this.$store.dispatch("updateLogin", true);
			this.setItem("isLogin", true);
			// 
			this.setItem("userInfo", res.data.profile);
			// vuex 
			this.$store.dispatch("saveUserInfo", res.data.profile);
			// 
			this.getUserSonglistBy(res.data.profile.userId);
		},

		// 
		getUserSonglistBy(uid) {
			getUserSonglist(uid).then(res => {
				this.setItem("userSongList", res.data.playlist);
				this.$store.dispatch("saveUserSongList", res.data.playlist);
			});
			getLikSongList(uid).then(res => {
				if (res.data.ids.length != 0) {
					this.$store.dispatch("saveLikeSongIds", res.data.ids);
				}
			});
		},

		// 
		getCaptcha() {
			// 
			if (this.ruleForm.phone.trim() === "") {
				this.$message({
					showClose: true,
					message: "",
					type: "warning",
					center: true,
				});
			} else {
				// 
				// checkPhoneNum(this.ruleForm.phone).then(res => {
				// if (res.data.exist === 1) {
				// 	this.$message({
				// 		showClose: true,
				// 		message: "",
				// 		type: "warning",
				// 		center: true,
				// 	});
				// } else if (res.data.exist === -1) {
				// 
				getAuthcode(this.ruleForm.phone).then(res => {
					switch (res.data.code) {
						case 200:
							this.$message({
								showClose: true,
								message: "",
								type: "success",
								center: true,
							});
							this.isGetCaptcha = true;
							let count = 60;
							this.tip = `${count}`;
							let timerC = setInterval(() => {
								count--;
								this.tip = `${count}`;
								if (count === 0) {
									this.isGetCaptcha = false;
									clearInterval(timerC);
								}
							}, 1000);
							break;
						case 400:
							this.$message({
								showClose: true,
								message: ":5",
								type: "warning",
								center: true,
							});
							break;
						case 405:
							this.$message({
								showClose: true,
								message: "",
								type: "warning",
								center: true,
							});
							break;
						default:
							this.$message({
								showClose: true,
								message: "",
								type: "warning",
								center: true,
							});
							break;
					}
				});
				// }
				// });
			}
		},

		//
		register(formName) {
			this.$refs[formName].validate(valid => {
				if (valid) {
					// 
					checkAuthcode(this.ruleForm.phone, this.ruleForm.captcha)
						.then(res => {
							// 
							if (res.data.code == 200) {
								register(this.ruleForm.phone, this.ruleForm.captcha, this.ruleForm.pass, this.ruleForm.nickname)
									.then(res => {
										// console.log(res);
										if (res.data.code == 200) {
											this.$message({
												showClose: true,
												message: "",
												type: "success",
												center: true,
											});
											setTimeout(() => {
												this.changeLoginWay(0);
											}, 500);
										}
									})
									.catch(err => {
										this.$message({
											showClose: true,
											message: "",
											type: "warning",
											center: true,
										});
									});
							}
						})
						.catch(err => {
							this.$message({
								showClose: true,
								message: "",
								type: "warning",
								center: true,
							});
						});
				} else {
					this.$message({
						showClose: true,
						message: "",
						type: "warning",
						center: true,
					});
				}
			});
		},
	},
};
</script>

