<template>
	<div>
		<div class="user-pop" ref="userpop">
			<div class="data-num">
				<div class="num-item">
					<h2>{{ userInfo.eventCount }}</h2>
					<div></div>
				</div>
				<div class="num-item" @click="toFollows">
					<h2>{{ userInfo.follows }}</h2>
					<div></div>
				</div>
				<div class="num-item" @click="toFansList">
					<h2>{{ userInfo.followeds | formatNum }}</h2>
					<div></div>
				</div>
			</div>
			<div style="text-align: center">
				<el-button round size="small"></el-button>
			</div>
			<ul class="userinfo-list">
				<li @click="toMyhome">
					<span><i class="iconfont icon-user"></i></span><i class="iconfont icon-xiayiye"></i>
				</li>
				<li @click="getUserRecordBy">
					<span><i class="iconfont icon-dengji"></i></span><i class="iconfont icon-xiayiye"></i>
				</li>
				<li>
					<span><i class="iconfont icon-shezhi"></i></span><i class="iconfont icon-xiayiye"></i>
				</li>
				<li @click="logout">
					<span><i class="iconfont icon-tuichu"></i></span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
import { logout } from "network/login/login";
import { getUserDetail } from "network/userdetail/userdetail";
import { mapGetters } from "vuex";
export default {
	name: "UserPop",
	computed: {
		...mapGetters(["userInfo"]),
	},
	created() {
		this.getUserDetailBy();
	},
	methods: {
		//
		getUserDetailBy() {
			getUserDetail(this.userInfo.userId).then(res => {
				this.setItem("userInfo", res.data.profile);
				// vuex 
				this.$store.dispatch("saveUserInfo", res.data.profile);
			});
		},
		// 
		toMyhome() {
			this.$router.push("/userdetail/" + this.userInfo.userId);
		},
		// 
		getUserRecordBy() {
			this.$router.push("/userdetail/record/" + this.userInfo.userId);
		},
		//
		toFollows() {
			this.$router.push({
				name: "Follows",
				params: {
					uid: this.userInfo.userId,
					uname: this.userInfo.nickname,
					follows: this.userInfo.follows,
				},
			});
		},
		//
		toFansList() {
			this.$router.push({
				name: "FansList",
				params: { uid: this.userInfo.userId, uname: this.userInfo.nickname },
			});
		},

		// 
		logout() {
			this.$confirm("?", "", {
				confirmButtonText: "",
				cancelButtonText: "",
				type: "warning",
			})
				.then(() => {
					logout().then(res => {
						this.$message({
							showClose: true,
							message: "",
							type: "success",
							center: true,
						});
						// 
						this.$store.dispatch("updateLogin", false);
						this.removeItem("isLogin");
						// 
						this.$store.dispatch("saveUserInfo", null);
						this.removeItem("userInfo");
						// 
						this.$store.dispatch("saveUserSongList", []);
						this.removeItem("userSongList");
						// id
						this.$store.dispatch("saveLikeSongIds", []);
						this.removeItem("likeSongIds");
					});
				})
				.catch(err => {
					err;
				});
		},
	},
	mounted() {
		// 
		document.addEventListener("mouseup", e => {
			let showuserpop = document.querySelector("#showuserpop");
			if (showuserpop) {
				if (!showuserpop.contains(e.target)) {
					this.$emit("closeuserPop");
				}
			}
		});
	},
};
</script>

<style lang="less" scoped>
.user-pop {
	position: absolute;
	top: 62px;
	right: 45px;
	box-shadow: 0px 0px 2px 2px #eef;
	background: #fff;
	border-radius: 5px;
	.data-num {
		display: flex;
		justify-content: center;
		margin: 10px 0;
		.num-item {
			padding: 0 15px;
			text-align: center;
			cursor: pointer;
		}
	}
	.userinfo-list {
		margin: 5px 0;
		li {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 5px 5px;
			&:hover {
				background: #eee;
				cursor: pointer;
			}
		}
	}
}
.user-pop ::after {
	position: absolute;
	top: -30px;
	left: 85px;
	content: "";
	width: 0;
	height: 0;
	border: 15px solid;
	border-left-color: transparent;
	border-top-color: transparent;
	border-right-color: transparent;
	border-bottom-color: #fff;
}
</style>
