import {request} from "../request";
// 
export function getUserDetail(uid){
    return request({
        url:'/user/detail',
        params:{
            uid,
            timestamp:Date.parse(new Date()),
        }
    })
}
// 
export function getUserSonglist(uid){
    return request({
        url:'/user/playlist',
        params:{
            uid,
            timestamp:Date.parse(new Date()),
            limit:1000,
        }
    })
}

// 
export function getUserFollows(uid,offset){
    return request({
        url:'/user/follows',
        params:{
            uid,
            offset,
            limit:48,
            timestamp:Date.parse(new Date()),
            
        }
    })
}
//
export function getUserFolloweds(uid,offset){
    return request({
        url:'/user/followeds',
        params:{
            uid,
            offset,
            limit:48,
            timestamp:Date.parse(new Date()),
        }
    })
}
// 
export function getUserRecord(uid,type){
    return request({
        url:'/user/record',
        params:{
            uid,
            type
        }
    })
}

// 
export function getUserEvent(uid){
    return request({
        url:'/user/event',
        params:{
            uid,
            limit:40
        }
    })
}
// 
export function subUser(t,id){
    return request({
        url:'/follow',
        params:{
            t,
            id,
            timestamp:Date.parse(new Date()),
        }
    })
}
