<template>
	<div class="comment">
		<!--  -->
		<div class="textinput">
			<el-input ref="commentContent" type="textarea" rows="3" maxlength="100" show-word-limit v-model.trim="content" :placeholder="placeholder" @blur="textareaBlur"> </el-input>
			<el-button round type="primary" size="small" icon="el-icon-position" class="el-btn" @click="submitComment"></el-button>
		</div>
		<!--  -->
		<div class="comment-list" ref="commentlist">
			<h2>{{ sortType === 3 ? "" : "" }}({{ commentCount | formatNum }})</h2>
			<div class="sort-category">
				<span v-for="(item, index) in sortCategory" :key="index" @click="changeSort(item.sortType, index)" :class="{ active: currentIndex === index }">{{ item.title }}</span>
			</div>
			<div class="new-comment" v-if="commentsList.length !== 0">
				<CommentItem
					v-for="(item, index) in commentsList"
					:key="index"
					:item="item"
					@toUserDetail="toUserDetail"
					@getReplyComment="getReplyComment"
					:commentType="commentType"
					:commentresId="commentresId"
				/>
				<Pagination :total="commentCount" :page-size="60" :current-page="currentPage" :scroll-top="false" @handleCurrentChange="handleCurrentChange" />
			</div>
			<div class="nocomment" v-else>
				<el-empty description="" :image-size="200"></el-empty>
			</div>
		</div>
	</div>
</template>
<script>
/*  */
import { getComment, getHotComment, getNewCommentList, sendComment, replyComment } from "network/comment/comment";
/*  */
import CommentItem from "./CommentItem.vue";
export default {
	name: "CommentPage",
	components: { CommentItem },
	props: {
		// 
		// 0:music 1:mv 2:playlist 3:album 5:video
		commentType: {
			type: Number,
		},
		// id:id
		commentresId: {
			type: [Number, String],
		},
	},
	data() {
		return {
			placeholder: "", //
			content: "", //
			commentsList: [], //
			commentCount: 0, //
			sortType: 3, //,2:,3:
			// 
			sortCategory: [
				{ title: "", sortType: 3 },
				{ title: "", sortType: 2 },
			],
			currentIndex: 0,
			currentPage: 1, //
			submitType: 1, // 12
			replyId: "", //id
			replyUname: "", //
		};
	},
	created() {
		// 
		this.getNewCommentBy();
	},
	watch: {
		// id
		commentresId() {
			this.getNewCommentBy();
			this.sortType = 3;
			this.currentIndex = 0;
			this.content = "";
			this.currentPage = 1;
		},
	},
	methods: {
		/* 
    
     */
		getHotCommentBy(page = 1) {
			let offset = (page - 1) * 60;
			getHotComment(this.commentType, this.commentresId, offset).then(res => {
				if (res.data.total != 0) {
					this.commentsList = res.data.hotComments;
					this.commentCount = res.data.total;
				} else {
					getComment(this.commentType, this.commentresId, page).then(res => {
						this.commentsList = res.data.data.comments || [];
						this.commentCount = res.data.data.totalCount;
					});
				}
			});
		},

		/* 
    
     */
		getNewCommentBy(page = 1) {
			let type = "";
			switch (this.commentType) {
				case 0:
					type = "music";
					break;
				case 1:
					type = "mv";
					break;
				case 2:
					type = "playlist";
					break;
				case 3:
					type = "album";
					break;
				case 5:
					type = "video";
					break;
			}
			let offset = (page - 1) * 60;
			getNewCommentList(type, this.commentresId, offset).then(res => {
				this.commentsList = res.data.comments;
				this.commentCount = res.data.total;
			});
		},
		/* 
    
    */
		changeSort(sortType, index) {
			this.currentIndex = index;
			this.sortType = sortType;
			this.currentPage = 1;
			if (sortType === 3) {
				// 
				this.getNewCommentBy();
			} else this.getHotCommentBy();
		},
		/* 
    
     */
		handleCurrentChange(page) {
			if (this.sortType === 3) {
				// 
				this.getNewCommentBy(page);
			} else this.getHotCommentBy(page);
			this.currentPage = page;
			this.$emit("changePage"); //
		},
		// 
		toUserDetail() {
			//  
			this.$emit("toUserDetail");
		},

		/* 
    
     */
		//.1 
		sendCommentBy() {
			sendComment(this.commentType, this.commentresId, this.content)
				.then(res => {
					// console.log(res);
					if (res.data.code === 200) {
						//  
						this.content = "";
						//  
						setTimeout(() => {
							this.changeSort(3, 0);
						}, 1000);
						this.$message({
							type: "success",
							message: "",
							center: true,
						});
					} else this.$message.error(",!");
				})
				.catch(err => {
					this.$message.error(",!");
				});
		},
		//.2 
		// 2.1id
		// 2.2
		// 2.3
		// 2.4
		getReplyComment(replyInfo) {
			this.replyId = replyInfo.commentId;
			this.replyUname = replyInfo.uname;
			// ()
			this.$emit("changePage");
			// 
			this.placeholder = `${replyInfo.uname}`;
			// 
			this.$refs.commentContent.focus();
			// 
			this.submitType = 2;
		},
		// 2.5 
		replyCommentBy() {
			// ()
			// let start = this.replyUname.length + 3;
			// let replyContent = this.content.slice(start);
			// 
			// if (replyContent.trim() === "") {
			//   this.$message({
			//     type: "warning",
			//     message: "",
			//     showClose: true,
			//     center: true,
			//   });
			// } else {
			// 
			replyComment(this.commentType, this.commentresId, this.content, this.replyId)
				.then(res => {
					if (res.data.code === 200) {
						// 
						this.content = ""; //
						this.placeholder = ""; //
						this.submitType = 1; //
						//  
						setTimeout(() => {
							this.changeSort(3, 0);
						}, 1000);
						this.$message({
							type: "success",
							message: "",
							center: true,
						});
					} else this.$message.error(",!");
				})
				.catch(err => {
					this.$message.error(",!");
				});
			// }
		},
		//.3 
		submitComment() {
			// 
			if (!this.$store.state.isLogin) {
				this.$message({
					type: "warning",
					message: "",
					showClose: true,
					center: true,
				});
				return;
			}
			// 
			if (this.content.trim() === "") {
				this.$message({
					type: "warning",
					message: "",
					showClose: true,
					center: true,
				});
				return;
			} else {
				// 
				if (this.submitType === 1) {
					// 
					this.sendCommentBy();
				} else {
					// 
					this.replyCommentBy();
				}
			}
		},
		// 
		textareaBlur() {
			if (this.content.trim() === "") {
				this.placeholder = "";
				this.submitType = 1;
			}
		},
	},
};
</script>

<style lang="less" scoped>
.comment {
	position: relative;
}
.textinput {
	text-align: right;
	.el-btn {
		margin-top: 10px;
	}
}
.sort-category {
	margin: 20px 0;
	span {
		padding-right: 10px;
		cursor: pointer;
	}
	.active {
		font-weight: 700;
	}
}
</style>
