import {request} from "../request";
// 
export function login(phone,password){
    return request({
        url:'/login/cellphone',
        params:{
            phone,
            password
        }
    })
}

// 
// key
export function getQrKey(){
    return request({
        url:'/login/qr/key',
        params:{
            timestamp:Date.parse(new Date())
        }
    })
}
// 
export function getLoginQr(key){
    return request({
        url:'/login/qr/create',
        params:{
            key,
            timestamp:Date.parse(new Date()),
            qrimg:true
        }
    })
}
// 
export function checkLoginQr(key){
    return request({
        url:'/login/qr/check',
        params:{
            key,
            timestamp:Date.parse(new Date())
        }
    })
}

// 
export function getUserAccount(){
    return request({
        url:'/user/account',
        params:{
            timestamp:Date.parse(new Date())
        }
    })
}
// 
export function dailySignin(){
    return request({
        url:'/daily_signin',
        params:{
            type:1
        }
    })
}

// 
export function logout(){
    return request({
        url:'/logout'
    })
}


//
//
export function getAuthcode(phone){
    return request({
        url:'/captcha/sent',
        params:{
            phone,
        }
    })
}
//
export function checkAuthcode(phone,captcha){
    return request({
        url:'/captcha/verify',
        params:{
            phone,
            captcha
        }
    })
}
//()
export function register(phone,captcha,password,nickname){
    return request({
        url:'/register/cellphone',
        params:{
            phone,
            captcha,
            password,
            nickname
        }
    })
}
// 
export function checkPhoneNum(phone){
    return request({
        url:'/cellphone/existence/check',
        params:{
            phone,
        }
    })
}
// 
export function initNickName(nickname){
    return request({
        url:'/activate/init/profile',
        params:{
            nickname,
        }
    })
}