import {request} from '../request'

// ()
export function getHotComment(type,id,offset){
    return request({
        url:'/comment/hot',
        params:{
            type,
            id,
            offset,
            limit:60,
            timestamp:Date.parse(new Date())
        }
    })
}
// 
export function getNewCommentList(type,id, offset) {
    return request({
      url: `/comment/${type}`,
      params: {
        id,
        offset,
        limit: 60,
        timestamp:Date.parse(new Date())
      },
    });
  }
// 
export function getComment(type,id,pageNo){
    return request({
        url:'/comment/new',
        params:{
            type,
            id,
            pageNo,
            sortType:2,
            pageSize:60,
            timestamp:Date.parse(new Date())
        }
    })
}
// 
export function sendComment(type,id,content){
    return request({
        url:'/comment',
        params:{
            t:1,
            type,
            id,
            content,
            timestamp:Date.parse(new Date())
        }
    })
}
// 
export function replyComment(type,id,content,commentId){
    return request({
        url:'/comment',
        params:{
            t:2,
            type,
            id,
            content,
            commentId,
            timestamp:Date.parse(new Date())
        }
    })
}
// 
export function isLikeComment(id,type,t,cid){
    return request({
        url:'/comment/like',
        params:{
            id,
            type,
            t, //1  ,0 
            cid,
            timestamp:Date.parse(new Date())
        }
    })
}
